.footer {
  max-width: 100%;
  padding: 0 200px 0 200px;
  margin: 65px auto 60px auto;
}

@media screen and (max-width: 1100px) {
  .footer {
    padding: 0 100px 0 100px;
  }
}

@media screen and (max-width: 760px) {
  .footer {
    padding: 0 19px 0 19px;
    margin: 48px auto 36px auto;
  }
}
